<template>
  <div class="content">
    <div class="left_plan">
      <div class="plan">
        <span class="p"></span>
      </div>
    </div>
    <div class="plan_line">
      <div
        class="plan_time"
        :class="index % 2 == 0?'left':'right'"
        v-for="(i,index) in list"
        :key="index"
        :style="`animation-delay:${index*0.2}s;`"
      >
        <div class="pt_thing">
          <p class="time">{{i.time}}</p>
          <p class="msg">{{i.msg}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      percent: 0,
      list: [
        {
          time: "1997.06.08",
          msg: "我出生了~"
        },
        {
          time: "2017.06 - 2018.07",
          msg:
            "在郑州卡农企业管理咨询有限公司，任职Web前端工程师，主要负责切图和页面的效果还原。由于负责事项较为简单，没有能力上的提升，所以提出辞职"
        },
        {
          time: "2018.12 - 至今",
          msg: "在上海拓悉信息科技公司，任职Web前端工程师，主要负责前端项目的完成以及技术问题的探讨和解决。公司隶属于申东集团的子公司，业务需求为金融类。此次经历对个人有很大的提升，主要项目有 东金中心官网、东金秀财、BMS后台管理系统等。但是由于业务需求变更，IT部门将要解散，预计在5 ~ 6月份会寻找新的工作。"
        }
      ]
    };
  },
  mounted() {},
  methods: {}
};
</script>

<style lang="scss" scoped>
@media screen and (min-width: 500px) {
  .pt_thing {
    min-width: 400px !important;
  }
}
.content {
  overflow: hidden;
  display: flex;
  justify-content: center;
}
.plan {
  width: 1px;
  height: 100%;
  background: #ccc;
  position: fixed;
  left: calc(50% - 0.5px);
  top: 0;
  transform: scaleY(0);
  opacity: 0;
  animation: plan 0.5s ease both 1;
  .p {
    position: absolute;
    top: 0;
    left: 0;
    display: inline-block;
    width: 100%;
    height: 0%;
    background: var(--base);
    animation: planP 3s ease 1s both 1;
  }
}
.plan_line {
  width: 1px;
  .plan_time {
    display: flex;
    &.left {
      justify-content: flex-end;
      animation: textMoveRight 0.5s ease both 1;
      .pt_thing {
        margin-right: 1.5rem;
        &::before {
          border-left: 10px solid white !important;
          right: -20px;
        }
      }
    }
    &.right {
      justify-content: flex-start;
      animation: textMoveLeft 0.5s ease both 1;
      .pt_thing {
        margin-left: 1.5rem;
        &::before {
          border-right: 10px solid white !important;
          left: -20px;
        }
      }
    }
    .pt_thing {
      filter: drop-shadow(2px 2px 3px #ccc);
      min-width: 150px;
      margin: 1rem;
      background: white;
      padding: 0.5rem;
      position: relative;
      &::before {
        content: "";
        width: 0px;
        height: 0px;
        //   background: red;
        border: 10px solid transparent;
        position: absolute;
      }
      .time {
        font-size: 0.88rem;
        padding-bottom: 0.2rem;
        margin-bottom: 0.2rem;
        border-bottom: 1px solid #f1f1f1;
        font-weight: bold;
      }
      .msg {
        font-weight: 100;
        font-size: 0.75rem;
        color: #5a5a5a;
      }
    }
  }
}
h1 {
  margin-left: 4rem;
}
@keyframes plan {
  100% {
    opacity: 1;
    transform: scaleY(1);
  }
}
@keyframes planP {
  25% {
    height: 30%;
  }
  50% {
    height: 60%;
  }
  75% {
    height: 85%;
  }
  100% {
    height: 100%;
  }
}
@keyframes textMoveRight {
  0% {
    opacity: 0;
    transform: translateX(-2rem);
  }
  100% {
    opacity: 1;
    transform: translateX(0rem);
  }
}
@keyframes textMoveLeft {
  0% {
    opacity: 0;
    transform: translateX(2rem);
  }
  100% {
    opacity: 1;
    transform: translateX(0rem);
  }
}
</style>


